import { getCartList, updateCart, delCart } from '@/api/cart'
export default {
  namespaced: true,
  state () {
    return {
      list: []
      // cartTotal: localStorage.getItem('cartTotal') || 0
    }
  },
  getters: {
    // 商品总数
    shopTotalNums (state) {
    //   return state.cartTotal
      return state.list.reduce((prev, item) => {
        return prev + item.goods_num
      }, 0)
    },
    // 筛选出选中的商品
    isShopNums (state) {
      return state.list.filter(item => item.isChecked === true)
    },
    // 选中商品的总数  (访问其他getters的数据)
    shopNums (state, getters) {
      return getters.isShopNums.reduce((prev, item) => {
        return prev + item.goods_num
      }, 0)
    },
    // 选中商品的总价
    shopPrices (state, getters) {
      return getters.isShopNums.reduce((prev, item) => {
        return prev + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2) // 计算结果保留2位小数
    },
    // 全选属性 如果全选返回true，否则是false
    isAll (state) {
      return state.list.every(item => item.isChecked)
    }
  },
  mutations: {
    updateFn (state, newValue) {
      state.list = newValue
    },
    // 切换选中状态
    toggleChecked (state, goodsId) {
      const theShop = state.list.find(item => item.goods_id === goodsId)
      theShop.isChecked = !theShop.isChecked
    },
    // 全选与反选
    isCheckedAll (state, newValue) { // 单行默认有return
      state.list.forEach(item => { item.isChecked = newValue })
    },
    // 更新通过id判断修改对应商品的数量
    updataCount (state, { goodsId, goodsNum }) {
      const theShop = state.list.find(item => item.goods_id === goodsId)
      theShop.goods_num = goodsNum
    },
    // vuex前端删除购物车数量
    delCartMutation (state, cartId) {
      state.list = state.list.filter(item => item.goods_id !== cartId)
    }
  },

  actions: {
    // 调用购物车列表接口
    async getCartAction (context, newValue) {
      const res = await getCartList()
      const { data } = res
      // forEach没有返回值并且直接改变原数组
      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('updateFn', data.list)
    },
    // 调用更新购物车数量接口
    async updateCountAction (context, obj) {
      const { goodsId, goodsNum, goodsSkuId } = obj
      await updateCart(goodsId, goodsNum, goodsSkuId)

      // 同步更新前端渲染
      context.commit('updataCount', {
        goodsId,
        goodsNum
      })
    },

    // 调用删除购物车商品接口
    async delCartAction (context) {
      // 调用getters中 被选中的商品 的方法
      const theShops = context.getters.isShopNums
      console.log(theShops)
      // 将得到的若干个商品id保存为数组
      const theGoodsId = theShops.map(item => item.id)
      // 然后调用接口传递这个数组
      await delCart(theGoodsId)
      // mutations中也删除，可以不用这么麻烦,直接调用获取商品列表方法重新渲染即可
      context.dispatch('getCartAction')
    }
  }
}
