<template>
    <div id="app">
        <!-- 路由出口 -->
        <router-view/>

      <van-tabbar active-color="#ee0a24" inactive-color="#000" route>
      <van-tabbar-item  to="/home" icon="wap-home-o">首页</van-tabbar-item>
      <van-tabbar-item  to="/category" icon="apps-o">分类页</van-tabbar-item>
      <van-tabbar-item  to="/cart" icon="shopping-cart-o" :badge="$store.getters['cart/shopTotalNums']">购物车</van-tabbar-item>
      <van-tabbar-item  to="/user" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
    </div>
</template>

<script>
import { getCartList } from '@/api/cart'
export default {
  name: 'layoutIndex',
  data () {
    return {
      // cartTotal: 0
    }
  },
  created () {
    // 判断是否登录用户有无token 调用购物车数量
    const userToken = this.$store.state.user.userinfo.token
    if (userToken) {
      // 调用vuex中的action方法
      this.$store.dispatch('cart/getCartAction')
    }
  },
  methods: {
    // 页面刷新调用购物车数量
    async getCartNums () {
      const { data } = await getCartList()
      this.cartTotal = data.cartTotal
    }
  },

  components: {}
}
</script>

