import request from '@/utils/request'
// 订单页面
export const getOrderApi = (mode, obj) => {
  // mode: cart =>传 obj { cartIds }
  // mode: buyNow =>传 obj { goodsId, goodsNum,goodsSkuId,}
  return request.get('/checkout/order', {
    params: {
      mode, // 两种模式 cart buyNow
      delivery: 10,
      couponId: 0,
      isUsePoints: 0,
      ...obj
    }
  })
}

// 提交订单
export const buyOrderApi = (mode, obj) => {
  // mode: cart =>传 obj { cartIds,remark }
  // mode: buyNow =>传 obj { goodsId, goodsNum,goodsSkuId,remark}
  return request.post('/checkout/submit', {
    mode,
    delivery: 10, // 物流方式
    couponId: 0, // 优惠券ID
    isUsePoints: 0, // 是否使用积分
    payType: 10, // 支付方式
    ...obj
  })
}

// 查询订单列表
export const getOrderListApi = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType,
      page
    }
  })
}
// 查询商品状态数量
export const getStatusCountsApi = () => {
  return request.get('/order/todoCounts')
}

// 取消订单
export const cancelOrderApi = (orderId) => {
  return request.post('/order/cancel', {
    orderId
  })
}
// 确认收货
export const confirmOrderApi = (orderId) => {
  return request.post('/order/receipt', {
    orderId
  })
}
