<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <!-- 购物车有商品状态 判断是否登录 -->
    <div class="isShow" v-if="$store.state.user.userinfo.token && shopTotalNums > 0 ">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{ shopTotalNums }}</i>件商品</span>
        <span @click="isEdit = !isEdit, isAllChecked" class="edit">
          <van-icon name="edit" />
          编辑
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in list" :key="item.goods_id">
          <van-checkbox @click="toggleFn(item.goods_id)" :value="item.isChecked"></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
              <!-- <CountBox :value="item.goods_num" @input="changeCount(item.goods_id, item.goods_num,item.goods_sku_id)"></CountBox> -->
              <CountBox :value="item.goods_num" @input="value => changeCount(item.goods_id, value, item.goods_sku_id)"></CountBox>
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div @click="isAllChecked" class="all-check">
           <!-- isAll在vuex中为getter属性 -->
          <van-checkbox :value = 'isAll' icon-size="18"></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ shopPrices }}</i></span>
          </div>
          <div v-if="!isEdit" class="goPay" @click="goPay"  :class="{ disabled: shopNums === 0 }">结算({{ shopNums }})</div>
          <div v-else class="delete" @click="delFn" :class="{ disabled: shopNums === 0 }">删除</div>
        </div>
      </div>
    </div>

    <!-- 空购物车状态 -->
    <div class="empty-cart" v-else>
    <img src="@/assets/empty.png" alt="">
    <div class="tips">
      您的购物车是空的, 快去逛逛吧
    </div>
    <div class="btn" @click="$router.push('/')">去逛逛</div>
  </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import CountBox from '@/components/CountBox.vue'
export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  data () {
    return {
      isEdit: false
    }
  },
  created () {
    // 判断是否登录用户有无token
    const userToken = this.$store.state.user.userinfo.token
    if (userToken) {
      // 调用vuex中的action方法
      this.$store.dispatch('cart/getCartAction')
    }
  },
  computed: {
    // 辅助函数获取vuex中的state值
    ...mapState('cart', ['list']),
    ...mapGetters('cart', ['shopTotalNums', 'isShopNums', 'shopNums', 'shopPrices', 'isAll']),
    // 筛选出选中商品的id
    selectedShop () {
      return this.isShopNums.map(item => item.id).join(',')
    }
  },
  methods: {
    // 切换选中状态
    toggleFn (goodsId) {
      this.$store.commit('cart/toggleChecked', goodsId)
    },
    // 全选框的全选与反选
    isAllChecked () { // 传全选按钮状态的取反值
      this.$store.commit('cart/isCheckedAll', !this.isAll)
    },
    // 修改购物车数量
    changeCount (goodsId, goodsNum, goodsSkuId) {
      console.log(goodsId, goodsNum, goodsSkuId)
      // 获取修改后商品的id、与数量，调用action去修改
      this.$store.dispatch('cart/updateCountAction', {
        goodsId, goodsNum, goodsSkuId
      })
    },
    // 删除购物车商品
    async  delFn () {
      if (this.shopNums === 0) return
      this.$dialog.confirm({
        title: '温馨提示',
        message: '您确认要删除吗？',
        confirmButtonText: '确认',
        cancelButtonText: '取消'
      })
        .then(async () => {
          this.$toast.success('删除成功')
          await this.$store.dispatch('cart/delCartAction')
          this.isEdit = false
        })
        .catch(() => {})
    },
    // 跳转到订单页面，并携带所需参数
    goPay () {
      if (this.shopNums > 0) {
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.isShopNums.map(item => item.id).join(',')
          }
        })
      }
    }
  },
  watch: {
    isEdit (value) {
      if (value === true) { // 如果编辑状态为true，则让所有框状态为false
        this.$store.commit('cart/isCheckedAll', false)
      } else {
        this.$store.commit('cart/isCheckedAll', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;

  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;

    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }

    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }

    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;

        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;

          span {
            font-size: 16px;
          }
        }

        .count-box {
          display: flex;
          width: 110px;

          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }

          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;

    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;

    .price {
      font-size: 14px;
      margin-right: 10px;

      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay,
    .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;

      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}

</style>
