// 将用户token与用户Id存储到本地localstorage
const USERINFO_KEY = 'user_info'

export const setInfo = (obj) => {
  localStorage.setItem(USERINFO_KEY, JSON.stringify(obj))
}
// 从本地localstorage中读取
export const getInfo = () => {
  return JSON.parse(localStorage.getItem(USERINFO_KEY)) || { token: '', userId: '' }
}

export const removeInfo = () => {
  localStorage.removeItem(USERINFO_KEY)
}

// 将搜索历史存储到本地localstorage
const SHOP_KEYWORD = 'keyword_info'

export const setKeyword = (keyword) => {
  localStorage.setItem(SHOP_KEYWORD, JSON.stringify(keyword))
}
export const getKeyword = () => {
  return JSON.parse(localStorage.getItem(SHOP_KEYWORD)) || []
}
export const removeKeyword = () => {
  localStorage.removeItem(SHOP_KEYWORD)
}

// 将地区数据存储到本地localstorage中
const REGION_DATA = 'regions'
export const setRegions = (regions) => {
  localStorage.setItem(REGION_DATA, JSON.stringify(regions))
}

export const getRegions = () => {
  return JSON.parse(localStorage.getItem(REGION_DATA))
}
