import Vue from 'vue'
import VueRouter from 'vue-router'
import { Toast } from 'vant'

// 导入vuex仓库
import store from '@/store'

// 导入一级对应组件
import Layout from '@/views/layout'
import Home from '@/views/layout/home'
import Category from '@/views/layout/category'
import Cart from '@/views/layout/cart'
import User from '@/views/layout/user'

// 导入二级对应组件
const Login = () => import('@/views/login')
const Myorder = () => import('@/views/myorder')
const Pay = () => import('@/views/pay')
const Prodetail = () => import('@/views/prodetail')
const Search = () => import('@/views/search')
const SearchList = () => import('@/views/search/list')
const MyAddress = () => import('@/views/address')
const NewAddress = () => import('@/views/address/newAddress')

Vue.use(VueRouter)

const router = new VueRouter({
  // 配置路由规则
  routes: [
    {
      path: '/',
      component: Layout, // 这里需要跳一级路由，然后重定向到二级路由
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    },
    // { path: '/layout', component: Layout },
    { path: '/login', component: Login },
    { path: '/myorder', component: Myorder },
    { path: '/pay', component: Pay },
    // 动态路由传参，确认将来是哪个商品
    { path: '/prodetail/:id', component: Prodetail },
    { path: '/search', component: Search },
    { path: '/list', component: SearchList },
    { path: '/myAddress', component: MyAddress },
    { path: '/newAddress', component: NewAddress }

  ]
  // routes
})

// 创建一个数组，存放需要判断用户权限的路由
const userUrl = ['/pay', '/myorder', '/myAddress']
// 全局前置导航守卫
router.beforeEach((to, from, next) => {
  // 判断userUrl中是否包含了to.path
  // if (userUrl.includes(to.path)) {
  // 判断是否有token
  const userToken = store.state.user.userinfo.token
  //     if (userToken) {
  //       next()
  //     } else {
  //       next('/login')
  //     }
  //   } else {
  //     next()
  //   }
  // })
  // 判断userUrl中是否包含了to.path
  if (!userUrl.includes(to.path)) {
    // 非权限页面直接放行
    next()
    return // 并停止往下走
  }
  // 是权限页面，判断是否有token
  if (userToken) {
    next()
    return // 停止往下走
  }
  Toast('请先登录')
  next('/login')
})

export default router
