import { setInfo, getInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state () {
    return {
      // userinfo: {
      //   token: '',
      //   userId: ''
      // }
      userinfo: getInfo()
    }
  },
  getters: {},
  mutations: {
    // 将获取到的token与用户id存入vuex
    getUserInfo (state, obj) {
      state.userinfo = obj
      setInfo(obj)
    }

  },
  actions: {
    clearOut (context) {
      context.commit('getUserInfo', {})
      context.commit('cart/updateFn', [], { root: true })
    }
  }
}
